import React from "react";
import styled from "styled-components";
import { Home } from "@styled-icons/material-outlined/Home";

import { Box } from "../../0-base/FlexBox";
import { Icon } from "../../0-base/Icon";

interface NavigationItemProps {
  children?: React.ReactNode;
}

const StyledNavigationItem = styled(Box).attrs({
  as: "a",
})`
  width: 260px;
  height: 55px;
  background-color: rgb(${(props) => props.theme.color.white});
  /* min-height: calc(100vh); */
  /* box-shadow: 0px 2.4px 28px rgba(33, 40, 50, 0.15); */
`;

const StyledTitle = styled.h2``;

const StyledIcon = styled(Icon).attrs({
  as: Home,
})`
  color: ${(props) => props.theme.color.gunmetal};
`;

export default function NavigationItem({
  children,
}: NavigationItemProps): JSX.Element {
  return (
    <StyledNavigationItem>
      <StyledIcon />
      <StyledTitle>프로필</StyledTitle>
    </StyledNavigationItem>
  );
}
